var $window = $(window);

function logIterate() {
    $(".section").each(function (index, section) {
        var $section = $(section);
        var sectionTop = $section.offset().top;
        var sectionBottom = $section.offset().top + $section.height();
        console.log(index + ": " + sectionTop + " " + sectionBottom + " scrollTop:" + $window.scrollTop());
    });
}

$("#my_div").on("click", function (event) {
    console.log("clicked");
    event.preventDefault();
    logIterate();

});

$(window).load(function(){
    logIterate();

});

